<!--  -->
<template>
  <div class="v-header">
    <h3>了不起的谢的博客</h3>
    <ul>
      <li>
        <router-link to="/home">
          <p>首页</p>
        </router-link>
      </li>
      <li>
        <router-link to="/classify">
          <p>分类</p>
        </router-link>
      </li>
      <li>
        <router-link to="/contacts">
          <p>联系我</p>
        </router-link>
      </li>
      <li>
        <router-link to="/notes">
          <p>随笔</p>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import router from '../router/index.js'
export default {
  router: router,
  data () {
    return {
    }
  },
  methods: {}
}

</script>
<style lang='scss' scoped>
.v-header{
  width: 100%;
  height: 160px;
  // position: fixed;
  // top: 0;
  // left: 0;
  background: #409EFF;
  color: #fff;
  h3{
    width: 80%;
    margin: 0 auto;
    line-height: 100px;
    font-size: 28px;
    text-align: center
  }
  ul{
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
    border-top: 1px solid #ccc;
    li:last-child {
      border-right:1px solid #ccc;
    }
    li{
      width: 25%;
      line-height: 60px;
      font-size: 24px;
      text-align: center;
      border-left: 1px solid #ccc;
      float: left;
      .router-link-active{
        color:#F56C6C;
        font-weight: 700;
        opacity: 0.5;
      }
      a{
        color: #fff;
      }
    }
  }
}
</style>



